// Colors

$lt-green: #A5C974;
$yellow: #F0D167;
$lt-blue: #E1F4FA;

body {
  font-size: 16px;
  background: $lt-blue;
}

.banner {
  background: $lt-green;
}

.navbar-color {
  background: $yellow;
}

.main-bkgrd {
  background: #fff;
}

.main-content {
  font-size: 1.2em;
}

.bef-exposed-form .form--inline > .form-item {
  float: none;
}

.filter-categories {
  summary {
    display: inline-block;
    margin: 8px 0;
    background: #ccc;
    padding: 4px 12px 5px 12px;
    font-size: 1.3em;
  }
  .form-inline .radio, .form-inline .checkbox {
    display: block;
  }

  button.form-submit {
    margin-top: 30px;
  }
}

.view-resource-search {
  h3 {
    font-size: 2.0em;
  }
  .views-row {
    margin-bottom: 25px;
  }
}

.views-row {
  margin-bottom: 20px;
}
